import useKey from '@rooks/use-key';
import clsx from 'clsx';
import React, {
    useEffect,
    useRef,
    useState,
    forwardRef,
    useImperativeHandle
} from 'react';
import { IconButton } from '@element/react-icon-button';
import { Icon } from '@element/react-icon';
import { Textfield } from '@element/react-textfield';
import { edsRandomId } from '@element/react-common';
import { formatDate } from './dateUtils';
import {
    DateInputDefaultProps,
    DateInputPropTypes
} from './generated/propTypes';

const DateInput = forwardRef((props, externalRef) => {
    const {
        // calendarIsOpen,
        className,
        disableClearing,
        disabled,
        format,
        id,
        label,
        onClear,
        onClose,
        // onOpen,
        onArrowDown,
        onFocus,
        onMouseDown,
        onTypedDate,
        // openOnFocus,
        parseFormats,
        placeholder,
        readOnly,
        required,
        selectedDate,
        textfieldProps,
        typedDate,
        locale,
        useUtc,
        variant,
        ...customProps
    } = props;

    const textfieldRef = useRef();
    const [formattedValue, setFormattedValue] = useState('');
    const [idActual] = useState(() => id || edsRandomId());

    const clearDate = event => {
        onClear?.(event);
    };

    const handleClearableOnKeyDown = event => {
        if (event.key === 'Enter') clearDate(event);
    };

    useImperativeHandle(externalRef, () => textfieldRef.current);

    // Attempt to parse a typed date
    const handleChanged = event => {
        onTypedDate?.(event.target.value, event);
    };

    const showClearAction = () => !disableClearing && formattedValue;

    useEffect(() => {
        if (!selectedDate && !typedDate) {
            setFormattedValue('');
            return;
        }

        if (typedDate) {
            setFormattedValue(typedDate);
            return;
        }

        setFormattedValue(
            typeof format === 'function'
                ? format(selectedDate)
                : formatDate(selectedDate, format, locale)
        );
    }, [format, locale, selectedDate, typedDate]);

    useKey(['ArrowDown'], e => {
        onArrowDown?.(e);
    });

    const handleFocus = e => {
        onFocus?.(e);
    };

    const handleMouseDown = e => {
        onMouseDown?.(e);
    };

    return (
        <Textfield
            autoComplete='off'
            className={clsx('lmnt', 'lmnt-datepicker-input', className)}
            disabled={disabled}
            id={idActual}
            label={label}
            leadingIcon={<Icon icon='event' tabIndex={-1} />}
            onChange={handleChanged}
            onFocus={handleFocus}
            onMouseDown={handleMouseDown}
            placeholder={placeholder}
            readOnly={readOnly}
            ref={textfieldRef}
            required={required}
            tabIndex={0}
            value={formattedValue}
            variant={variant}
            trailingIcon={
                showClearAction() ? (
                    <IconButton
                        type='button'
                        icon='clear'
                        dense
                        onClick={clearDate}
                        onKeyDown={handleClearableOnKeyDown}
                    />
                ) : undefined
            }
            {...textfieldProps}
            {...customProps}
        />
    );
});

DateInput.displayName = 'DateInput';
DateInput.propTypes = DateInputPropTypes;
DateInput.defaultProps = DateInputDefaultProps;

export default DateInput;
