import {
    edsRandomId,
    HoistingContext,
    useIsomorphicLayoutEffect
} from '@element/react-common';
import { createPopper } from '@popperjs/core';
import React, {
    useEffect,
    useRef,
    useState,
    useImperativeHandle,
    forwardRef,
    useCallback,
    useContext
} from 'react';
import useKey from '@rooks/use-key';
import {
    firstOfMonth,
    getVariant,
    getReadonly,
    getContainerClasses,
    getInitialDate,
    getVisibleMonthYear,
    parseDate,
    isValidDate
} from './dateUtils';
import DateInput from './DateInput';
import Calendar from './Calendar';
import {
    DatepickerDefaultProps,
    DatepickerPropTypes
} from './generated/propTypes';

const Datepicker = forwardRef((props, externalRef) => {
    const {
        alwaysOpen,
        beforeCalendarHeader,
        calendarProps,
        centuryBehavior,
        centurySlidingOffset,
        className,
        dayCellTemplate,
        disableChangeMonth,
        disableChangeYear,
        disableClearing,
        disabled,
        disabledDates,
        filled,
        format,
        fullMonthName,
        headerContent,
        highlightedDates,
        hoisted,
        id,
        inputTimeZone, // TODO: Unfinished feature
        label,
        leadingContent,
        locale,
        maxDate,
        maxQuickSelectYear,
        minDate,
        minQuickSelectYear,
        mondayFirst,
        onChange,
        onClear,
        onClose,
        onOpen,
        onSelect,
        onSelectDisable,
        onSelectDisabled,
        onSelectMonth,
        onSelectYear,
        openDate,
        outlined,
        outputTimeZone, // TODO: Unfinished feature
        parseFormats,
        placeholder,
        portalContainer,
        primary,
        readOnly,
        required,
        resetCalendarOnClose,
        secondary,
        sideContent,
        style,
        textfieldProps,
        trailingContent,
        typeable,
        useUtc,
        value,
        variant,
        ...customProps
    } = props;

    const hoistingContext = useContext(HoistingContext);
    const initialDate = getInitialDate(openDate || value, value, useUtc);
    const [idActual] = useState(() => id || edsRandomId());
    const [isOpen, setIsOpen] = useState(false);
    const [selectedDate, setSelectedDate] = useState(value);
    const [variantActual, setVariantActual] = useState(variant);
    const [typedDate, setTypedDate] = useState('');
    const calendarRef = useRef();
    const containerRef = useRef();
    const dateInputRef = useRef();
    const popperRef = useRef();
    const [visibleDate, setVisibleDate] = useState(initialDate);
    const [openOnFocus, setOpenOnFocus] = useState(true);
    const hoistedActual = hoisted || hoistingContext?.enabled;

    useIsomorphicLayoutEffect(() => {
        const modifiers = [
            {
                name: 'flip',
                options: {
                    enabled: true,
                    fallbackPlacements: [
                        'bottom-start',
                        'top-start',
                        'bottom-end',
                        'top-end'
                    ]
                }
            }
        ];
        popperRef.current = createPopper(
            dateInputRef.current.container,
            calendarRef.current,
            {
                placement: 'bottom-start',
                modifiers
            }
        );

        return () => {
            popperRef?.current?.destroy();
        };
    }, []);

    const showCalendar = useCallback(() => {
        setIsOpen(true);

        calendarRef.current?.setAttribute('data-show', '');
        popperRef.current?.setOptions(options => ({
            ...options,
            modifiers: [
                ...options.modifiers,
                { name: 'eventListeners', enabled: true }
            ]
        }));
        popperRef.current?.update();
    }, []);

    const hideCalendar = useCallback(() => {
        setIsOpen(false);
        calendarRef.current?.removeAttribute('data-show');
        popperRef.current?.setOptions(options => ({
            ...options,
            modifiers: [
                ...options.modifiers,
                { name: 'eventListeners', enabled: false }
            ]
        }));
    }, []);

    const handleClosed = useCallback(
        (emitEvent, doSetFocus) => {
            if (alwaysOpen) return;
            setTypedDate('');

            hideCalendar();
            onClose?.(emitEvent);
            if (doSetFocus) dateInputRef.current?.focus();
            setOpenOnFocus(true);
            if (resetCalendarOnClose)
                setVisibleDate(firstOfMonth(selectedDate, useUtc));
        },
        [
            alwaysOpen,
            hideCalendar,
            onClose,
            resetCalendarOnClose,
            selectedDate,
            useUtc
        ]
    );

    useKey(['Escape'], event => {
        setOpenOnFocus(false);
        handleClosed(event, true);
    });

    const handleBlur = useCallback(
        event => {
            // Prevent the datepicker from automatically closing the calendar
            // when it is clicked on. This allows the Select controls to work
            // as well as any customized content to be interacted with.
            if (
                calendarRef?.current?.contains(event.relatedTarget) ||
                dateInputRef?.current?.input.parentElement.contains(
                    event.relatedTarget
                )
            )
                return;
            handleClosed(event);
        },
        [handleClosed]
    );

    const handleClear = useCallback(
        event => {
            setTypedDate('');
            setSelectedDate(null);
            setVisibleDate(firstOfMonth(null, useUtc));
            onSelect?.(null, event);
            onChange?.(null, event);
            onClear?.(event);
        },
        [onChange, onClear, onSelect, useUtc]
    );

    const handleSetDate = useCallback(
        (timestamp, event) => {
            const date = new Date(timestamp);
            setSelectedDate(date);
            setVisibleDate(firstOfMonth(date, useUtc));
            onSelect?.(date, event);
            onChange?.(date, event);
        },
        [onChange, onSelect, useUtc]
    );

    const handleTypedDate = useCallback(
        (val, event) => {
            if (!val) {
                handleClear(event);
                return;
            }
            setTypedDate(val);
            const parsed = parseDate(
                val,
                parseFormats,
                format,
                locale,
                centuryBehavior === 'age' ? centurySlidingOffset : -1
            );

            if (isValidDate(parsed, parseFormats, format, locale)) {
                handleSetDate(parsed.getTime(), event);
            }
        },
        [
            centuryBehavior,
            centurySlidingOffset,
            format,
            handleClear,
            handleSetDate,
            locale,
            parseFormats
        ]
    );

    const handleSelectDate = useCallback(
        (date, event) => {
            setTypedDate('');
            handleSetDate(date.getTime(), event);
            handleClosed(event);
        },
        [handleClosed, handleSetDate]
    );

    const handleSelectMonth = useCallback(
        (date, event) => {
            setVisibleDate(firstOfMonth(date, useUtc));
            onSelectMonth?.(date, event);
        },
        [onSelectMonth, useUtc]
    );

    const handleSelectYear = useCallback(
        (date, event) => {
            setVisibleDate(firstOfMonth(date, useUtc));
            onSelectYear?.(date, event);
        },
        [onSelectYear, useUtc]
    );

    const handleSelectDisabledDate = useCallback(
        (date, event) => {
            onSelectDisable?.(date, event) || onSelectDisabled?.(date, event);
        },
        [onSelectDisable, onSelectDisabled]
    );

    useEffect(() => {
        if (alwaysOpen) showCalendar();
        setIsOpen(alwaysOpen);
    }, [alwaysOpen, showCalendar]);

    useEffect(() => {
        setSelectedDate(value);
        setVisibleDate(getVisibleMonthYear(value, useUtc));
    }, [useUtc, value]);

    useEffect(() => {
        if (openDate === undefined) return;
        setVisibleDate(firstOfMonth(openDate, useUtc));
    }, [openDate, useUtc]);

    useImperativeHandle(externalRef, () => ({
        openCalendar: () => showCalendar(),
        closeCalendar: () => hideCalendar(),
        container: containerRef.current,
        input: dateInputRef.current,
        calendar: calendarRef.current
    }));

    useEffect(() => {
        setVariantActual(
            getVariant(primary, filled, secondary, outlined, variant)
        );
    }, [variant, primary, filled, secondary, outlined]);

    useEffect(() => {
        /* eslint-disable no-undef */
        window.addEventListener('reset', handleClear);

        return () => {
            window.removeEventListener('reset', handleClear);
        };
        /* eslint-enable no-undef */
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const handleMouseDown = () => {
        showCalendar();
    };

    const handleArrowDown = () => {
        if (!isOpen) showCalendar();
    };

    const handleFocus = () => {
        if (openOnFocus && !isOpen) onOpen?.();
    };

    return (
        <div
            ref={containerRef}
            className={getContainerClasses(className, props)}
            onBlur={handleBlur}
            style={{ position: 'relative', ...style }}
        >
            <DateInput
                disableClearing={disableClearing}
                disabled={disabled}
                format={format}
                id={idActual}
                label={label}
                locale={locale}
                onClear={handleClear}
                onClose={handleClosed}
                onMouseDown={handleMouseDown}
                onArrowDown={handleArrowDown}
                onFocus={handleFocus}
                onTypedDate={handleTypedDate}
                placeholder={placeholder}
                readOnly={getReadonly(typeable, readOnly)}
                ref={dateInputRef}
                required={required}
                selectedDate={selectedDate}
                textfieldProps={textfieldProps}
                typedDate={typedDate}
                useUtc={useUtc}
                variant={variantActual}
                {...customProps}
            />

            <Calendar
                dayCellTemplate={dayCellTemplate}
                disableChangeMonth={disableChangeMonth}
                disableChangeYear={disableChangeYear}
                disabledDates={disabledDates}
                floating
                fullMonthName={fullMonthName}
                headerContent={headerContent}
                highlightedDates={highlightedDates}
                hoisted={hoistedActual}
                leadingContent={beforeCalendarHeader || leadingContent}
                locale={locale}
                maxDate={maxDate}
                maxQuickSelectYear={maxQuickSelectYear}
                minDate={minDate}
                minQuickSelectYear={minQuickSelectYear}
                mondayFirst={mondayFirst}
                onSelectDate={handleSelectDate}
                onSelectDisabled={handleSelectDisabledDate}
                onSelectMonth={handleSelectMonth}
                onSelectYear={handleSelectYear}
                portalContainer={portalContainer}
                ref={calendarRef}
                selectedDate={selectedDate}
                sideContent={sideContent}
                style={{ display: isOpen ? 'block' : 'none' }}
                trailingContent={trailingContent}
                useUtc={useUtc}
                visibleDate={visibleDate}
                {...calendarProps}
            />
        </div>
    );
});

Datepicker.displayName = 'Datepicker';
Datepicker.propTypes = DatepickerPropTypes;
Datepicker.defaultProps = DatepickerDefaultProps;

export default Datepicker;
