import React, {
    cloneElement,
    forwardRef,
    useCallback,
    useEffect,
    useImperativeHandle,
    useMemo,
    useRef,
    useState
} from 'react';
import { TypoCaption } from '@element/react-typography';
import { IconButton } from '@element/react-icon-button';
import { Select } from '@element/react-select';
import { usePrevious, useIsomorphicLayoutEffect } from '@element/react-common';
import { Portal } from '@element/react-portal';
import {
    getBlankDays,
    getCalendarContainerClasses,
    getCalendarDayClasses,
    getCalendarDays,
    getCalendarDaysOfWeek,
    getMonth,
    getQuickSelectMonths,
    getQuickSelectYears,
    getYear,
    incrementMonth,
    isNextMonthDisabled,
    isPreviousMonthDisabled,
    matchMonthByVal,
    setFullYear,
    setMonth
} from './dateUtils';
import { CalendarDefaultProps, CalendarPropTypes } from './generated/propTypes';

const Calendar = forwardRef((props, externalRef) => {
    const {
        className,
        dayCellTemplate,
        disableChangeMonth,
        disableChangeYear,
        disabledDates,
        floating,
        fullMonthName,
        headerContent,
        highlightedDates,
        hoisted,
        isRtl,
        leadingContent,
        locale,
        maxDate,
        maxQuickSelectYear,
        minQuickSelectYear,
        mondayFirst,
        onSelectDate,
        onSelectDisabled,
        onSelectMonth,
        onSelectYear,
        portalContainer,
        selectedDate,
        sideContent,
        trailingContent,
        minDate,
        useUtc,
        visibleDate,
        visibleMonthYear,
        ...customProps
    } = props;

    const ref = useRef();
    const daysRef = useRef();

    useImperativeHandle(externalRef, () => ref.current);

    // Remove in v6 when visibleMonthYear is removed.
    const visibleDateActual = visibleMonthYear || visibleDate;
    const [leftNavDisabled, setLeftNavDisabled] = useState(false);
    const [rightNavDisabled, setRightNavDisabled] = useState(false);
    const [blankDays, setBlankDays] = useState([]);
    const [daysOfWeek, setDaysOfWeek] = useState([]);
    const [months, setMonths] = useState([]);
    const [daysOfMonth, setDaysOfMonth] = useState([]);
    const [years, setYears] = useState([]);
    const [selectedMonth, setSelectedMonth] = useState();
    const [selectedYear, setSelectedYear] = useState();
    const [lastDayFocus, setLastDayFocus] = useState();
    const [firstDayFocus, setFirstDayFocus] = useState();

    useEffect(() => {
        setYears(
            getQuickSelectYears({
                maxQuickSelectYear,
                minQuickSelectYear,
                useUtc,
                visibleMonthYear: visibleDateActual
            })
        );
    }, [maxQuickSelectYear, minQuickSelectYear, useUtc, visibleDateActual]);

    useEffect(() => {
        setMonths(getQuickSelectMonths(locale, fullMonthName));
    }, [locale, fullMonthName]);

    useEffect(() => {
        setDaysOfWeek(getCalendarDaysOfWeek(locale, mondayFirst));
    }, [locale, mondayFirst]);

    useEffect(() => {
        // wait until months and years are loaded before setting selected values, otherwise we get an error with Select
        if (months?.length === 0 || years?.length === 0) return;
        setSelectedMonth(getMonth(visibleDateActual, useUtc));
        setSelectedYear(getYear(visibleDateActual, useUtc));
    }, [months, useUtc, visibleDateActual, years]);

    const handleSelectDate = (date, event) => {
        if (date.isDisabled) {
            onSelectDisabled?.(date, event);
            return;
        }
        onSelectDate?.(new Date(date.timestamp), event);
    };

    const handleChangeMonth = useCallback(
        (incrementBy, event, doSetLastDayFocus, doSetFirstDayFocus) => {
            onSelectMonth &&
                onSelectMonth(
                    incrementMonth(incrementBy, visibleDateActual),
                    event
                );
            if (doSetLastDayFocus) setLastDayFocus(true);
            if (doSetFirstDayFocus) setFirstDayFocus(true);
        },
        [onSelectMonth, visibleDateActual]
    );

    const daysOfMonthPrev = usePrevious(daysOfMonth, daysOfMonth);
    useIsomorphicLayoutEffect(() => {
        if (lastDayFocus) {
            if (daysOfMonthPrev[0].month !== daysOfMonth[0].month) {
                daysRef.current
                    ?.querySelector(`#date-${daysOfMonth.length}`)
                    ?.focus();
                setLastDayFocus(false);
                return;
            }
        }
        if (firstDayFocus) {
            if (daysOfMonthPrev[0].month !== daysOfMonth[0].month) {
                daysRef.current?.querySelector(`#date-1`)?.focus();
                setFirstDayFocus(false);
            }
        }
    }, [daysOfMonth, daysOfMonthPrev, firstDayFocus, lastDayFocus]);

    const handleShowPreviousMonth = useCallback(
        (event, doSetLastDayFocus) => {
            if (
                !isPreviousMonthDisabled(
                    disableChangeMonth,
                    disableChangeYear,
                    disabledDates,
                    useUtc,
                    visibleDateActual
                )
            ) {
                handleChangeMonth(-1, event, doSetLastDayFocus);
            }
        },
        [
            handleChangeMonth,
            disableChangeMonth,
            disableChangeYear,
            disabledDates,
            useUtc,
            visibleDateActual
        ]
    );

    const handleShowNextMonth = useCallback(
        (event, doSetLastDayFocus, doSetFirstDayFocus) => {
            if (
                !isNextMonthDisabled(
                    disableChangeMonth,
                    disableChangeYear,
                    disabledDates,
                    useUtc,
                    visibleDateActual
                )
            ) {
                handleChangeMonth(
                    +1,
                    event,
                    doSetLastDayFocus,
                    doSetFirstDayFocus
                );
            }
        },
        [
            handleChangeMonth,
            disableChangeMonth,
            disableChangeYear,
            disabledDates,
            useUtc,
            visibleDateActual
        ]
    );

    const handleSelectMonth = useCallback(
        (newMonth, event) => {
            onSelectMonth(
                setMonth(visibleDateActual, newMonth.value, useUtc),
                event
            );
        },
        [onSelectMonth, visibleDateActual, useUtc]
    );

    const handleSelectYear = useCallback(
        (newYear, event) => {
            if (!newYear) return;
            onSelectYear(
                setFullYear(visibleDateActual, newYear, useUtc),
                event
            );
        },
        [onSelectYear, visibleDateActual, useUtc]
    );

    // This prevents the calendar from closing when the user clicks on it
    const handlePreventDefault = useCallback(e => {
        if (ref?.current?.contains(e.target)) return;
        e.preventDefault();
    }, []);

    useEffect(() => {
        const days = getCalendarDays({
            disabledDates,
            highlightedDates,
            maxDate,
            minDate,
            selectedDate,
            useUtc,
            visibleMonthYear: visibleDateActual
        });

        setDaysOfMonth(days);
    }, [
        disabledDates,
        highlightedDates,
        maxDate,
        minDate,
        selectedDate,
        useUtc,
        visibleDateActual
    ]);

    useEffect(() => {
        setBlankDays(
            getBlankDays({
                visibleMonthYear: visibleDateActual,
                useUtc,
                mondayFirst
            }) || []
        );
    }, [mondayFirst, visibleDateActual, useUtc]);

    useEffect(() => {
        setLeftNavDisabled(
            isRtl
                ? isNextMonthDisabled(
                      disableChangeMonth,
                      disableChangeYear,
                      disabledDates,
                      useUtc,
                      visibleDateActual
                  )
                : isPreviousMonthDisabled(
                      disableChangeMonth,
                      disableChangeYear,
                      disabledDates,
                      useUtc,
                      visibleDateActual
                  )
        );
    }, [
        disableChangeMonth,
        disableChangeYear,
        disabledDates,
        isRtl,
        useUtc,
        visibleDateActual
    ]);

    useEffect(() => {
        setRightNavDisabled(
            isRtl
                ? isPreviousMonthDisabled(
                      disableChangeMonth,
                      disableChangeYear,
                      disabledDates,
                      useUtc,
                      visibleDateActual
                  )
                : isNextMonthDisabled(
                      disableChangeMonth,
                      disableChangeYear,
                      disabledDates,
                      useUtc,
                      visibleDateActual
                  )
        );
    }, [
        disableChangeMonth,
        disableChangeYear,
        disabledDates,
        isRtl,
        useUtc,
        visibleDateActual
    ]);

    // We can give some flexibility to how they want to handle the side content of the calendar
    const SideContentContainer = useMemo(
        () => () => {
            if (typeof sideContent === 'function') {
                return sideContent({ date: selectedDate });
            }
            if (typeof sideContent === 'object') {
                return cloneElement(sideContent, {
                    date: selectedDate
                });
            }
            return sideContent;
        },
        [selectedDate, sideContent]
    );

    const handleGoToNextDay = e => {
        e.preventDefault();
        const currentDay = parseInt(e.target.id.split('-')[1], 10);
        if (currentDay + 1 > daysOfMonth.length) {
            handleShowNextMonth(e, false, true);
            return;
        }
        const nextDay = daysRef.current?.querySelector(
            `#date-${currentDay + 1}`
        );
        nextDay.focus();
    };

    const handleGoToPrevDay = e => {
        e.preventDefault();
        const currentDay = parseInt(e.target.id.split('-')[1], 10);
        if (currentDay - 1 < 1) {
            handleShowPreviousMonth(e, true);
            return;
        }
        const previousDay = daysRef.current?.querySelector(
            `#date-${currentDay - 1}`
        );
        previousDay.focus();
    };

    const handleGoToNextWeek = e => {
        e.preventDefault();
        const currentDay = parseInt(e.target.id.split('-')[1], 10);
        const nextDate = currentDay + 7;
        if (nextDate > daysOfMonth.length) {
            handleShowNextMonth(e, false, true);
            return;
        }

        daysRef.current?.querySelector(`#date-${nextDate}`)?.focus();
    };

    const handleGoToPreviousWeek = e => {
        e.preventDefault();
        const currentDay = parseInt(e.target.id.split('-')[1], 10);
        const prevDate = currentDay - 7;
        if (prevDate < 1) {
            handleShowPreviousMonth(e, true);
            return;
        }

        daysRef.current?.querySelector(`#date-${prevDate}`)?.focus();
    };

    const handleDayKeyPress = e => {
        switch (e.key) {
            case 'ArrowLeft':
                handleGoToPrevDay(e);
                break;
            case 'ArrowRight':
                handleGoToNextDay(e);
                break;
            case 'ArrowDown':
                handleGoToNextWeek(e);
                break;
            case 'ArrowUp':
                handleGoToPreviousWeek(e);
                break;

            default:
        }
    };

    return (
        <Portal disabled={!hoisted} container={portalContainer}>
            <div
                ref={ref}
                className={getCalendarContainerClasses(
                    floating,
                    sideContent,
                    className
                )}
                onMouseDownCapture={handlePreventDefault}
                {...customProps}
            >
                <div className='lmnt-datepicker__content'>
                    {leadingContent}
                    <header className='lmnt-datepicker__calendar-header'>
                        <IconButton
                            type='button'
                            className='lmnt-datepicker__calendar-header-nav'
                            dense
                            icon='keyboard_arrow_left'
                            disabled={leftNavDisabled}
                            onClick={handleShowPreviousMonth}
                        />
                        <Select
                            value={matchMonthByVal(selectedMonth, months)}
                            dense
                            label=''
                            variant='outlined'
                            onChange={handleSelectMonth}
                            disabled={disableChangeMonth}
                            menuMaxHeight='250px'
                            options={months}
                            textKey='label'
                            valueKey='value'
                            className='lmnt-datepicker-calender__month-select'
                        />
                        <Select
                            dense
                            label=''
                            value={selectedYear}
                            variant='outlined'
                            onChange={handleSelectYear}
                            disabled={disableChangeYear}
                            menuMaxHeight='250px'
                            options={years}
                            className='lmnt-datepicker-calender__year-select'
                        />
                        <IconButton
                            type='button'
                            dense
                            icon='keyboard_arrow_right'
                            disabled={rightNavDisabled}
                            onClick={handleShowNextMonth}
                        />
                        {headerContent}
                    </header>
                    <div
                        className='lmnt-datepicker__calendar-surface'
                        ref={daysRef}
                    >
                        {daysOfWeek.map(d => (
                            <TypoCaption
                                className='lmnt-datepicker__day-header'
                                key={`dp-dow-${d.value}`}
                            >
                                {d.label}
                            </TypoCaption>
                        ))}
                        {blankDays.map(day => (
                            <span
                                className='lmnt-datepicker__cell lmnt-datepicker__cell--day lmnt-datepicker__cell--blank'
                                key={`dp-bd-${day}`}
                            />
                        ))}
                        {daysOfMonth.map(day => (
                            <button
                                key={`dp-dow-${day.timestamp}`}
                                data-day={day.date}
                                type='button'
                                id={`date-${day.date}`}
                                onClick={event => handleSelectDate(day, event)}
                                onKeyDown={handleDayKeyPress}
                                className={getCalendarDayClasses(day)}
                            >
                                {dayCellTemplate(day)}
                            </button>
                        ))}
                    </div>
                    {trailingContent}
                </div>
                {sideContent && (
                    <div className='lmnt-datepicker__side-content'>
                        <SideContentContainer />
                    </div>
                )}
            </div>
        </Portal>
    );
});

Calendar.displayName = 'Calendar';
Calendar.propTypes = CalendarPropTypes;
Calendar.defaultProps = CalendarDefaultProps;

export default Calendar;
